@import url(https://fonts.googleapis.com/css2?family=Libre+Baskerville:ital@0;1&family=Source+Serif+Pro:wght@300;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --accent: #ffbf46;
  --dark: #648381;
  --darker: #575761;
  --light: #8acb88;
  --lighter: #ffffff;
  --ssp: "Source Serif Pro";
  --lb: "Libre Baskerville";
}

.paymentWrapper{
    justify-content:center;
    align-items: center;
    margin-left: 90%;
    
    padding-left: 10px;
    padding-right: 10px;
    padding-bottom: 15px;

    border: solid black 3px;
    border-radius: 10px;
    background-color: lightgoldenrodyellow;
    width:-webkit-fit-content;
    width:-moz-fit-content;
    width:fit-content;
}
.default{
    margin-top: 20px;
    width: 100%;
  }
  
  .text {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  
  h1 {
    font-size: 30px;
    font-family: var(--ssp);
    color: var(--darker);
  }
  h2 {
    font-size: 25px;
    font-family: var(--ssp);
    color: var(--darker);
  }
  
  h3 {
    font-size: 20px;
    color: var(--dark);
  }
.display-board {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 90%;
    height: 1000 px;
    margin:auto;
    margin-top: 5%;
    justify-content: center;
}

.product-container {
    display: flex;
    flex-direction: column;
    justify-content:center;
    align-items: center;
    margin: 1rem;
    overflow: hidden;
    width:300px;
    box-shadow: 0px 0px 15px -5px;
}

.image-container img{
    margin: 0.5rem;
    margin-left: 1rem;
    overflow: hidden;
    width:90%;
    height: 200px;
    border: inset black 2px;
    border-radius: 5px;
}

.product-content{
    margin: 1rem;
    margin-top: 0.5rem;
}

.btn {
    margin: 0.5rem;
}
.container {
  margin-top: 20px;
  width: 100%;
}

.text {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

h2 {
  font-size: 30px;
  font-family: var(--ssp);
  color: var(--darker);
}
h4 {
  font-size: 25px;
  font-family: var(--ssp);
  color: var(--darker);
}

p {
  font-size: 20px;
  color: var(--dark);
}

.grandWrapper {
  display: flex;
}

.navbar {
  display: flex;
  flex-direction: column;
  width: 50%;
}

img {
  width: 130px;
  height: 130px;
  margin-left: 1%;
}

.links {
  display: flex;
  flex-direction: row;
  justify-self: right;
  margin-top: 1%;
}

.soloLink {
  margin: 0% 0% 0% 6%;
  color: var(--light);
  font-family: var(--ssp);
  text-decoration: none;
  font-weight: 600;
  font-size: 20px;
}

.soloLink:hover {
  color: var(--dark);
  text-decoration: line-through;
}

.navbar h1 {
  font-family: var(--ssp), Arial, Helvetica, sans-serif;
  font-style: bold;
  font-size: 60px;
  font-weight: 700;
  color: var(--light);
  margin: 1% 0% 0% 1%;
}

.cont {
  width: 100%;
  height: 15%;
  display: flex;
  position: absolute;
  background-color: var(--light);
  margin-top: 250px;
  margin-left: -8px;
}
.f-links {
  width: 100%;
  display: flex;
  align-items: stretch;
  justify-content: space-evenly;
  margin-top: 10px;
}

a {
  text-decoration: none;
  color: var(--darker);
}

a:hover {
  color: var(--lighter);
}

li {
  list-style-type: none;
  color: var(--dark);
  text-align: center;
}
a:hover {
  color: var(--light);
}

